import React, { useState } from 'react';
import { IonApp, IonContent, IonHeader, IonLabel, IonTabs, IonTitle, IonToolbar, IonTabBar, IonIcon, IonRouterOutlet, IonTabButton } from '@ionic/react';
import { Route, Redirect } from 'react-router';
import { IonReactRouter } from '@ionic/react-router';
import { book, chatbubbleEllipses, person } from 'ionicons/icons'

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

/* Components */
import TestComponent from './components/TestComponent';

/* Pages */
import ProfilePage from './pages/ProfilePage';


// import  {darkThemeContext, ThemeContextConsumer, ThemeContextProvider} from './AppContextProvider';

const App: React.FC = () =>{ 
  const prefersDark = window.matchMedia('(prefers-color-scheme: dark)');
  console.log(prefersDark.matches);
  const [darkTheme, useDarkTheme] = useState(prefersDark.matches);
  
  if(prefersDark.matches) document.body.classList.add("dark");

  return (
  <IonApp>
    
    <IonHeader>
      <IonToolbar  color="primary">
          <IonTitle>
              ROLER
          </IonTitle>
      </IonToolbar>
    </IonHeader>
    <IonContent>
    <IonReactRouter>
        <IonTabs>
            <IonRouterOutlet>
              <Route path="/" render={() => <Redirect to="/tabs/chats" />} exact={true} />
              <Route path="/roler" render={() => <Redirect to="/tabs/chats" />} exact={true} />
              <Route path="/tabs/chats" render={() => <TestComponent />} exact={true} />
              <Route path="/tabs/profile" render={() => <ProfilePage dark={darkTheme}/>} exact={true} />
            </IonRouterOutlet>
          
            {/* contact list */}
            <IonTabBar  slot="bottom">
              <IonTabButton tab="chats" href="/tabs/chats">
                  <IonIcon icon={book} />
                  <IonLabel>
                      Roleplays
                  </IonLabel>
              </IonTabButton>
              <IonTabButton tab="roleplays" href="/tabs/roleplays">
                  <IonIcon icon={chatbubbleEllipses} />
                  <IonLabel>
                      Chats
                  </IonLabel>
              </IonTabButton>
              <IonTabButton tab="profile" href="/tabs/profile">
                  <IonIcon icon={person} />
                  <IonLabel>
                      Profile
                  </IonLabel>
              </IonTabButton>
            </IonTabBar>
          </IonTabs>
        </IonReactRouter>

    </IonContent>
  </IonApp>
)}

export default App;