import { IonContent, IonIcon, IonItem, IonLabel, IonList, IonListHeader } from '@ionic/react';
import { sunny, moon } from 'ionicons/icons';
import React from 'react';

const ProfilePage: React.FC<{dark: Boolean}> = (props) =>{
    
    return(
        <IonContent>
            <IonList>
                <IonListHeader>
                    Settings
                </IonListHeader>
                <IonItem>
                    <IonLabel>
                        {props.dark ? "Dark" : "Light" } Mode

                    </IonLabel>
                    <IonIcon slot="end" icon={props.dark ? moon : sunny}/>
                </IonItem>
            </IonList>
        </IonContent>
    )


    
}

export default ProfilePage;